import { useState, useEffect, useContext } from "react";
import AppCtx from "../context"
import { Button, Card, Container, Form } from "react-bootstrap";
import "./style.css"
import { useNavigate } from "react-router-dom";
import { XCircle } from "react-bootstrap-icons";


function Auth() {
	const {
		setToken,
		api,
		user,
		setUser,
		setUserId,
		setPostsSrvAll,
		setUserInfoObj
	} = useContext(AppCtx);
	//   
	const navigate = useNavigate()
	//state       //
	const [regOrSign, setRegOrSign] = useState(false);
	//    //
	const [email, setEmail] = useState("");
	const [pwd, setPwd] = useState("");
	const [testPwd, setTestPwd] = useState("");
	const [regName, setRegName] = useState("");

	//   //
	const clearForm = () => {
		setRegName("");
		setEmail("");
		setPwd("");
		setTestPwd("")
	}
	//    
	const regOrSignExit = () => {
		navigate("/")
		setUserInfoObj({})
		clearForm()
	}
	//  state       
	// false  ,  
	const regOrSignState = () => {
		if (regOrSign) {
			setRegOrSign(false)
			clearForm()
		}
		else {
			setRegOrSign(true)
			clearForm()
		}
	}

	useEffect(() => {
		document.title = ' TravelBlog';
	  }, []);

	//   color:       
	const testAccess = {
		color: pwd === testPwd ? "forestgreen" : "crimson"
	}

	useEffect(() => {
		if (user) {
			setToken(localStorage.getItem("travelBlogToken"));
			setUserId(localStorage.getItem("travelBlogId"));

		}
		else {
			setToken("");
			setUserId("");
		}
	}, [user])

	//   //
	const regOrSignSubmit = async (e) => {
		e.preventDefault();
		//  body  fetch
		let body = {
			email: email,
			password: pwd
		}
		if (regOrSign) {
			////
			//console.log("")//
			//  body  fetch
			body.name = regName;
			body.group = "group-12";
			//  fetch  
			let fetchReg = await api.reg(body)
			//      body name  group   fetch   
			if (!fetchReg.err) {
				delete body.name;
				delete body.group;
			} else {
				//          
				return alert(`  ${fetchReg.message}`)
			}
		}
		////
		//console.log("")//
		const fetchAuth = await api.auth(body)
		if (!fetchAuth.err) {
			//console.log(fetchAuth);//
			localStorage.setItem("travelBlogUser", fetchAuth.data.name)
			localStorage.setItem("travelBlogToken", fetchAuth.token);
			localStorage.setItem("travelBlogId", fetchAuth.data._id);
			localStorage.setItem("travelBlogUserInfo", JSON.stringify(fetchAuth.data))
			localStorage.setItem("travelPostsAll", JSON.stringify(true))
			setPostsSrvAll(true)
			setToken(fetchAuth.token);
			setUser(fetchAuth.data.name)
			setUserId(fetchAuth.data._id)
			setUserInfoObj(fetchAuth.data)
			clearForm();
			navigate("/posts")
		} else {
			return alert(`  ${fetchAuth.message}`)
		}
	}

	return (<>

		<Container className="authSign_container">
			<Card className="authSign_Card">
				<Card.Header className="d-flex justify-content-between" as="h5">
					{regOrSign ? "" : "  "}
					<XCircle title="" style={{ color: "crimson", cursor: "pointer" }} onClick={regOrSignExit} />
				</Card.Header>
				<Card.Body>
					<Form onSubmit={regOrSignSubmit}>
						{regOrSign &&
							<Form.Control placeholder=" " className="mt-" as="input" type="text" required={true} value={regName} onChange={(e) => setRegName(e.currentTarget.value)} />
						}
						<Form.Control placeholder=" email" className="mt-3" as="input" type="email" required={true} value={email} onChange={(e) => setEmail(e.currentTarget.value)} />
						<Form.Control placeholder=" " className="mt-3" as="input" type="password" required={true} value={pwd} onChange={(e) => setPwd(e.currentTarget.value)} />
						{regOrSign &&
							<Form.Control placeholder=" " className="mt-3" as="input" type="password" required={true} value={testPwd} onChange={(e) => setTestPwd(e.currentTarget.value)}
								style={testAccess} />
						}
						<Card.Text className="d-flex justify-content-between mt-3">
							<Button size="sm" type="button" variant="outline-danger" onClick={regOrSignExit}></Button>
							<Button size="sm" type="button" variant="outline-info" onClick={regOrSignState}>{regOrSign ? "" : " "}</Button>
							<Button size="sm" type="submit" variant="outline-success"
								disabled={regOrSign && (!pwd || pwd !== testPwd || regName.trim() === "")}
							>{regOrSign ? "" : ""}</Button>
						</Card.Text>
					</Form>
				</Card.Body>
				<Card.Footer className="d-flex justify-content-center">
				</Card.Footer>
			</Card>
		</Container >
	</>)

}

export default Auth;