import { useState, useEffect } from "react";
import { Container, Row } from 'react-bootstrap';
////
import './index.css';
////
import Api from "./api"
import AppCtx from "./context";

//    06.06.2023 //
import './App.css';
//   
import NavbarMenu from './components/NavbarHeader';
// routes
import RoutesBlog from "./routes";
import allCountry from "./country.js"; 


function App() {
	const [user, setUser] = useState(localStorage.getItem("travelBlogUser"));
  const [userId, setUserId] = useState(localStorage.getItem("travelBlogId"));
  const [token, setToken] = useState(localStorage.getItem("travelBlogToken"));
  const [api, setApi] = useState(new Api(token)); //   api   
/* 	const [apiCountry, setApiCountry] = useState(new CountryApi()); //   api     */
	const [country, setCountry] =useState(allCountry); //    
	const [elPost, setElPost] = useState({}); //         
	const [userInfoObj, setUserInfoObj] =useState(JSON.parse(localStorage.getItem("travelBlogUserInfo"))); //    ]
  const [postsSrv, setPostsSrv] = useState([]); //    
	const [textSearch, setTextSearch] = useState("") //    
	const [myPostsSrv, setMyPostsSrv] = useState([]); //        ]
	const [postsSrvAll, setPostsSrvAll] = useState(JSON.parse(localStorage.getItem("travelPostsAll"))); //        "DiplomLk12"

	useEffect(() => {
    setApi(new Api(token))
  }, [token]) 
//          "DiplomLk12"

  //     //
   useEffect(() => {
    if (api.token) {
      api.getAllPosts()
        .then(data => {
					if (postsSrvAll===true) {
						setPostsSrv(data);						
						localStorage.setItem("travelPostsAll",JSON.stringify(true))
					}
					else{
						setPostsSrv(data?.filter(elx=>elx.tags?.includes("DiplomLk12")));
						localStorage.setItem("travelPostsAll",JSON.stringify(false))
					}
					console.log(data);
				})
			}
			else{
				console.log(" ");
			}
  }, [api.token, postsSrvAll]) 
		return (

	<Container className="container_body">
		<AppCtx.Provider value={{
			token,
			setToken,
			api,
			country,
			setUser,
			userId,
			setUserId,
			userInfoObj,
			setUserInfoObj,
			elPost, 
			setElPost,
			postsSrv,
			setPostsSrv,
			postsSrvAll,
			setPostsSrvAll,
			myPostsSrv,
			setMyPostsSrv,
			textSearch, 
			setTextSearch,
		}}>
			<Row className="justify-content-center"><NavbarMenu/></Row>
			<Row><RoutesBlog/></Row>

		</AppCtx.Provider>
	</Container>
  );
}

export default App;
