class Api {

	constructor(token) {
			this.path = "https://api.react-learning.ru";
			this.token = token;
	}

	//  //
	setHeaders(isCT = false, noToken = false) {
			const headerObj = {
					"Authorization": `Bearer ${this.token}`
			}
			if (isCT) {
					headerObj["Content-Type"] = "application/json"
			}
			if (noToken) {
					delete headerObj["Authorization"]
			}
			return headerObj;
	}

	setBody(body) {
			return JSON.stringify(body);
	}

	//   //
	//   //
	getAllPosts() {
			return fetch(`${this.path}/v2/group-12/posts`, {
					headers: this.setHeaders()
			}).then(res => res.json())
	}

	

	//  //
	addPost(body) {
			return fetch(`${this.path}/v2/group-12/posts`, {
					method: "POST",
					headers: this.setHeaders(true),
					body: this.setBody(body)
			}).then(res => res.json())
	}

	//   ID//
	getSinglePost(postId) {
			return fetch(`${this.path}/v2/group-12/posts/${postId}`, {
					headers: this.setHeaders()
			}).then(res => res.json())
	}

	//  //
	updSinglePost(postId, body) {
			return fetch(`${this.path}/v2/group-12/posts/${postId}`, {
					method: "PATCH",
					headers: this.setHeaders(true),
					body: this.setBody(body)
			}).then(res => res.json())
	}

	// //
	delSinglePost(postId) {
			return fetch(`${this.path}/v2/group-12/posts/${postId}`, {
					method: "DELETE",
					headers: this.setHeaders()
			}).then(res => res.json())
	}

	//   //
	setLike(postId, isLike) {
			return fetch(`${this.path}/v2/group-12/posts/likes/${postId}`, {
					method: isLike ? "PUT" : "DELETE",
					headers: this.setHeaders(true)
			}).then(res => res.json())

	}

	//   //
	// //
	reg(body) {
			return fetch(`${this.path}/signup`, {
					method: "POST",
					headers: this.setHeaders(true, true),
					body: this.setBody(body)
			}).then(res => res.json())
	}

	// //
	auth(body) {
			return fetch(`${this.path}/signin`, {
					method: "POST",
					headers: this.setHeaders(true, true),
					body: this.setBody(body)
			}).then(res => res.json())
	}

	//      //
	getProfile() {
			return fetch(`${this.path}/users/me`, {
					headers: this.setHeaders()
			}).then(res => res.json())
	}
	getProfileId(id) {
		return fetch(`${this.path}/users/${id}`, {
				headers: this.setHeaders(false,false)
		}).then(res => res.json())
}

	//    (  updImg -  ,    -   )//
	updProfile(body, updImg = false) {
			return fetch(`${this.path}/users/me/${updImg ? "avatar" : ""}`, {
					method: "PATCH",
					headers: this.setHeaders(true),
					body: this.setBody(body)
			}).then(res => res.json())
	}




	//========//

	addReview(id, body) {
			return fetch(`${this.path}/v2/group-12/posts/comments/${id}`, {
					method: "POST",
					headers: this.setHeaders(true),
					body: this.setBody(body)
			}).then(res => res.json())
	}

	delReview(productId, reviewId) {
			return fetch(`${this.path}/v2/group-12/posts/comments/${productId}/${reviewId}`, {
					method: "DELETE",
					headers: this.setHeaders()
			}).then(res => res.json())
	}


	/*    API  , 
	GET https://api.react-learning.ru/v2/:groupId/posts/search/?query=<   title> //   
	
	GET https://api.react-learning.ru/v2/:groupId/posts/paginate?page=< >&limit=<    >&query=<   title> // 
	
	POST https://api.react-learning.ru/v2/:groupId/posts/comments/:postId //    id
	
	DELETE https://api.react-learning.ru/v2/:groupId/posts/comments/:postId/:commentId //    id
	
	GET https://api.react-learning.ru/v2/:groupId/posts/comments/ //   
	GET https://api.react-learning.ru/v2/:groupId/posts/comments/:postId //    .
	
	*/
}

export default Api;