import { useContext, useEffect } from 'react';
import { Form, Row } from 'react-bootstrap';
import AppCtx from "../context.js"

const Setting = () => {
	const {
		setPostsSrvAll,
		postsSrvAll
	} = useContext(AppCtx);

	useEffect(() => {
		document.title = " ";
	  }, []);


	return (
		<Row>
			<Form>
				<h4 style={{ marginBottom: "15px" }}> </h4>
				<Form.Check
					className="mb-3"// prettier-ignore
					type="switch"
					label={`      "DiplomLk12"`}
					checked={postsSrvAll}
					onChange={e => { }}
					onClick={e => {
						if (e.currentTarget.checked) {
							setPostsSrvAll(true)
						}
						else {
							setPostsSrvAll(false)
						}
					}
					}
				/>
			</Form>

		</Row>
	);
}

export default Setting;
